.card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
  overflow: hidden; 
  position: relative;
}

.card img {
  width: 100%; 
  height: 300px; 
  border-radius: 8px 8px 0 0; 
}

.card-content {
  display: flex;
  flex-direction: column; 
  justify-content: space-between; 
  padding: 16px;
 
}

 .card-body{
  padding: 16px;
 }
.card h2 {
  font-size: 1.2rem;
