}

.card p {
  font-size: 1rem;
  color: #666;
}

.card .rating {
  font-size: 1rem;
  color: #FFD700;
  margin-left: 8px; 
}

.card-content h3 {
  display: inline-block;
}

.card-content h3 a {
  text-decoration: none; 
  color: #8D7B68;
  min-width: 44px;
  min-height: 44px;
  display: inline-block;
}


.image-container {
  position: relative; 
}

.city {
  position: absolute; 
  top: 8px; 
  left: 8px; 
  background-color: #8D7B68; 
  padding: 4px 8px; 
  border-radius: 4px; 
  font-size: 14px; 
  z-index: 1; 
  color: #ffffff !important;
}


.detail {
  display: grid;
  grid-template-areas: "detailHeader detailInfo" "detailOverview detailOverview" "detailReviews detailReviews";
  gap: 15px;
  font-size: 15px;
}

.detail > div {
  margin: auto;
}

.detail__header {
  grid-area: detailHeader;
}

.detail__poster {
  width: 100%;
}

.detail__title {
  color: #0d7583;
}

.detail__info {
  grid-area: detailInfo;
  padding: 10px;
  text-align: left;
  margin-left: 10px !important;
}

.detail__info > div {
  margin-top: 0.5em;
}

.detail__info > div > ul {
  padding: 5px;
  list-style-type: none;
}

.detail__overview {
  border-top: #0d7583;
  padding: 25px;
  grid-area: detailOverview;
  text-align: justify;
}

.detail__reviews {
  grid-area: detailReviews;
  padding: 25px;
  text-align: left;
  margin-left: 0 !important;
}

.info__categories > h4 {
  margin: 0.5em 0;
}


.like {
  font-size: 18px;
